import React from 'react';
import * as ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import { render } from 'react-dom';
import {
    DisplayText,
    Paragraph,
    SectionHeading,
    TextInput,
    Textarea,
    FieldGroup,
    RadioButtonField,
    Form,
    Select,
    Option
} from '@contentful/forma-36-react-components';
import { init, locations } from 'contentful-ui-extensions-sdk';
import '@contentful/forma-36-react-components/dist/styles.css';
import '@contentful/forma-36-fcss/dist/styles.css';
import './index.css';

/**
 * To use this demo create a Content Type with the following fields:
 *  title: Short text
 *  body: Long text
 *  hasAbstract: Boolean
 *  abstract: Long text
 *
 *  See https://github.com/contentful/create-contentful-extension/blob/master/docs/examples/entry-editor-content-model.json for details.
 */
export default function App(props) {
    console.log(props);

    const fields = props.sdk.entry.fields;
    const [formData, setFormData] = React.useState({
        contentType: fields.contentType.getValue()
    });

    const [isOpen, setOpen] = React.useState(false);
    const defaults = {
        text: false,
        list: false,
        imageCollection: false
    };
    const [visible, setVisible] = React.useState(defaults);

    const handleChange = event => {
        let data = Object.assign({}, defaults);

        switch (event.target.value) {
            case 'Text':
                data.text = true;
            break;
            case 'List':
                data.list = true;
            break;
            case 'Image Collection':
                data.imageCollection = true;
            break;
        }

        setVisible(data);
    };

    return (
        <Form className="f36-margin--l">
            <DisplayText>Entry extension demo</DisplayText>
            <Paragraph>
                This demo uses a single UI Extension to render the whole editor for an entry.
            </Paragraph>

            <SectionHeading>Content Type (required)</SectionHeading>

            <Select
                name="contentType"
                id="field-content-type"
                onChange={handleChange}
            >

                {
                    fields.contentType.validations[0].in.map((value, idx) => {
                        return (
                            <Option value={value} key={idx}>{value}</Option>
                        );
                    })
                }

            </Select>

            <div>header stuff here (and stays here)</div>

            { visible.text && (

                <div>put text stuff here</div>

            )}

            { visible.list && (

                <div>put list stuff here</div>

            )}

            { visible.imageCollection && (

                <div>put image collection stuff here</div>

            )}

        </Form>
    );
}

init(sdk => {
    if (sdk.location.is(locations.LOCATION_ENTRY_EDITOR)) {
        render(<App sdk={sdk} />, document.getElementById('root'));
    }
});